/* ===================================
   FIX GLOBAL ‚Äî Emp√™che la page de 
   d√©passer la largeur ‚Üí navbar mobile OK
=================================== */

.about-page {
  max-width: 100%;
  overflow-x: hidden; /* üî• Fix principal */
}

.about-header {
  max-width: 100%;
  overflow-x: hidden;
}

.about-image-section {
  max-width: 100%;
  overflow-x: hidden;
  flex-wrap: wrap; /* üî• √©vite l'√©largissement en mobile */
}

.about-big-image {
  max-width: 100%; /* üî• emp√™che d√©bordement largeur */
}

/* ============================
   HEADER GLOBAL
============================ */
.about-header {
  width: 100%;
  height: 60vh;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  align-items: flex-end;
}

.about-header .overlay {
  width: 100%;
  height: 100%;
  /* padding: 3rem 2rem; */
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0.25),
    transparent
  );
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
}

/* TITRE HEADER */
.about-title {
  color: #fff;
  font-size: 3rem;
  font-weight: 700;
  margin: 0;
  text-align: left;
  animation: fadeUp 0.8s ease-out forwards;
  text-align: center;
  margin-bottom: 2rem;
  font-family: "Libertinus Math", serif;
}

/* TEXTE HEADER */
.texte-header {
  color: #ffffff;
  font-size: 1.25rem;
  max-width: 700px;
  line-height: 1.6;
  margin: 0;
  opacity: 0.92;
  font-weight: 300;
  text-align: left;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.45);
  animation: fadeUp 1.1s ease-out forwards;
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .about-title {
    font-size: 2.2rem;
  }
  .about-header {
    height: 50vh;
  }
  .texte-header {
    font-size: 1rem;
    max-width: 95%;
  }
}

/* ============================
   SECTION TEXTE
============================ */
.about-section {
  max-width: 1100px;

  margin: auto;
  /* background: red; */
}

/* .about-section h2 {
  font-size: 2rem;
  color: var(--accent);
  margin-bottom: 1rem;
} */

.about-section p {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;

  line-height: 1.9;
  color: var(--text);
}

/* ============================
   WRAPPER ATELIER (GRIS)
============================ */
.atelier-wrapper {
  background: var(--card-bg);
  border-radius: 14px;
  margin: auto;
  max-width: 1100px;

  margin-bottom: -4rem;
}

/* .atelier-wrapper h2 {
  text-align: center;
  font-size: 2rem;
  color: var(--accent);
  margin-bottom: 2.5rem;
  font-weight: 700;
} */

/* ============================
   LIGNES ALTERN√âES
============================ */
.atelier-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2.5rem;
  margin-bottom: 4rem;
}

.atelier-row.reverse {
  flex-direction: row-reverse;
}

.atelier {
  border: 1px solid var(--text);
  border-radius: 14px;
}

/* TEXTE */
.atelier-text p {
  font-size: 1.35rem;
  line-height: 1.7;
  max-width: 480px;
  font-weight: 400;
  color: var(--text);
  margin-left: 20px;
  margin-right: 10px;
}

/* IMAGES GRAND FORMAT */
.atelier-img {
  width: 70%;
  height: 340px;
  object-fit: cover;
  border-radius: 14px;
  box-shadow: var(--shadow);
}

/* Responsive Atelier Rows */
@media (max-width: 900px) {
  .atelier-row,
  .atelier-row.reverse {
    flex-direction: column !important;
    text-align: center;
  }

  .atelier-img {
    width: 100%;
    height: 260px;
  }

  .atelier-text p {
    max-width: 100%;
  }
}

/* ============================
   SECTION CAMION (GRANDE IMAGE)
============================ */
.atelier-services {
  max-width: 1100px;
  margin: 3rem auto;
}

.item-camion {
  position: relative;
  width: 100%;
}

/* item-camion h2 {
  font-size: 2rem;
  color: var(--accent);
  margin-bottom: 1.2rem;
  text-align: center;
 
} */

.camion-atelier {
  width: 100%;
  max-height: 480px;
  object-fit: cover;
  border-radius: 14px;
  box-shadow: var(--shadow);
}

/* Texte sur image */
.item-camion p {
  position: absolute;
  bottom: 25px;
  left: 25px;
  padding: 1rem 1.4rem;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 1.25rem;
  line-height: 1.5;
  max-width: 70%;
  border-radius: 10px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.4);
}

@media (max-width: 768px) {
  .item-camion p {
    font-size: 1rem;
    max-width: 85%;
    bottom: 15px;
    left: 15px;
  }
}

@keyframes slideHorizontal {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}

/* ============================
   CARTE T√âMOIGNAGE
============================ */
.testimonial-card {
  flex: 0 0 480px;
  background: var(--card-bg);
  border: 2px solid var(--gold);
  border-radius: 16px;
  padding: 1.8rem 1.4rem;
  box-shadow: var(--shadow);
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.testimonial-card:hover {
  transform: translateY(-6px) scale(1.02);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.18);
}

/* ============================
   TEXTE T√âMOIGNAGE
============================ */
.testimonial-text {
  font-style: italic;
  color: var(--text);
  font-size: 1.1rem;
  line-height: 1.65;
  margin-bottom: 1rem;
  position: relative;
}

.testimonial-text::before {
  content: "‚Äú";
  font-size: 2.4rem;
  color: var(--gold);
  position: absolute;
  left: -12px;
  top: -18px;
  opacity: 0.55;
}

/* ============================
   NOM
============================ */
.testimonial-name {
  font-weight: 700;
  color: var(--accent);
  font-size: 1rem;
  text-align: right;
  margin-top: 1rem;
}

/* ============================
   FONDU SUR LES C√îT√âS
============================ */
.testimonials-carousel::before,
.testimonials-carousel::after {
  content: "";
  position: absolute;
  top: 0;
  width: 90px;
  height: 100%;
  z-index: 3;
  pointer-events: none;
}

.testimonials-carousel::before {
  left: 0;
  background: linear-gradient(to right, var(--bg) 60%, transparent);
}

.testimonials-carousel::after {
  right: 0;
  background: linear-gradient(to left, var(--bg) 60%, transparent);
}

/* ============================
   PAUSE AU SURVOL
============================ */
.testimonials-carousel:hover .carousel-track {
  animation-play-state: paused;
}

/* ============================
   CTA
============================ */
.apropos-cta {
  text-align: center;
  background: var(--accent);
  padding: 3rem 2rem;
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  width: 83%;
  margin: 3rem auto;
}

.apropos-cta h2 {
  color: var(--accent-contrast);
  font-size: 2rem;
}

.apropos-cta p {
  color: var(--accent-contrast);
  font-size: 1.1rem;
}

.apropos-cta button {
  background: var(--accent);
  color: var(--accent-contrast);
  padding: 0.9rem 2rem;
  border-radius: var(--radius);
  border: none;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.apropos-cta button:hover {
  background: var(--gold);
  transform: scale(1.05);
}

/* ============================
   RESPONSIVE
============================ */
@media (max-width: 768px) {
  .testimonial-card {
    flex: 0 0 280px;
    padding: 1.4rem;
  }

  .testimonial-text {
    font-size: 1rem;
  }

  .testimonials-carousel::before,
  .testimonials-carousel::after {
    width: 40px;
  }
}

/* ============================
   SECTION IMAGES FINALES
============================ */
.about-image-section {
  padding: 2rem 1.5rem;
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}

.about-big-image {
  width: 35%;
  border-radius: 14px;
  box-shadow: var(--shadow);
  object-fit: cover;
  animation: fade 0.8s ease-out forwards;
}

/* ============================
   ANIMATIONS
============================ */
.fade-in {
  opacity: 0;
  animation: fade 0.7s ease-out forwards;
}

@keyframes fade {
  to {
    opacity: 1;
  }
}
